









export default {
	name: 'uni-icon',
	props: {
		type: {
			type: String,
			default: ''
		},
		color: {
			type: String,
			default: '#333333'
		},
		size: {
			type: [Number, String],
			default: 16
		}
	},
	methods: {
		_onClick() {
			this.$emit('click');
		}
	}
};
