















import SectionTitle from './SectionTitle';

export default {
	data() {
		return {
			mvDetail:null,
			mvLen:0,
		};
	},
	methods:{
		getMvDetail({offset}){
			uni.showLoading({
				title: '加载中',
			});

			return uni.request({
				url: 'https://api.bzqll.com/music/netease/topMvList',
				data: {
					key:this.key,
					limit:10,
					offset
				}
			})











		},
		waitFn(){
			console.log('wait');
		},
		errorFn(){
			console.log('error');
		}
	},
	onLoad(){
		this.getMvDetail({offset:this.mvLen}).then(res=>{
			if(res[1].data.code == 200){
				this.mvDetail = res[1].data.data
				uni.hideLoading ()
			}
		})
	},
	onPullDownRefresh(){
		this.mvLen = 0
		this.getMvDetail({offset:this.mvLen}).then(res=>{
			if(res[1].data.code == 200){
				this.mvDetail = res[1].data.data
				uni.hideLoading ()
				uni.stopPullDownRefresh()
			}
		})
	},
	onReachBottom(){
		this.mvLen+=10
		if(this.mvLen >= 100){
			uni.showToast({
				title: '已无更多内容',
				icon:'none',
				duration: 2000
			})
			return
		}
		this.getMvDetail({offset:this.mvLen}).then(res=>{
			if(res[1].data.code == 200){
				this.mvDetail.push(...res[1].data.data)
				uni.hideLoading ()
			}
		})
	},
	components:{
		'section-title':SectionTitle
	}
}
