


























import {mapState} from 'vuex'
import {uniIcon} from "@dcloudio/uni-ui"
import SongList from './SongList.vue';

export default {
	data() {
		return {
			showDesc:'',
			showIcon:'icon-down',
			detail:null
		};
	},
	computed:{
		...mapState(['listId'])
	},
	methods:{
		showDescFn(){			
			if(this.showDesc == ''){
				this.showDesc = 'active'
				this.showIcon = 'icon-top'
			}else if(this.showDesc == 'active'){
				this.showDesc = ''
				this.showIcon = 'icon-down'
			}
		},
		getListData(){
			uni.showLoading({
				title:'加载中...'
			})

			uni.request({
				url: 'https://api.bzqll.com/music/netease/songList',
				data: {
					key:this.key,
					id:this.listId
				},
			}).then(res=>{
				if(res[1].data.code == 200){
					this.detail = res[1].data.data
					uni.hideLoading ()
					uni.stopPullDownRefresh()
				}
			})
















		}
	},
	onLoad() {
		this.getListData()
	},
	onPullDownRefresh(){
		this.getListData()
	},
	components:{
		'song-list':SongList,
		uniIcon
	}
}
