










import SongList from './SongList';
export default {
	data() {
		return {
			timer:null,
			dataLen:0,
			searchVal:'',
			songs:null
		};
	},
	methods:{
		getSearchData({s,limit,offset}){
			uni.showLoading({
				title: '加载中',
			});

			return uni.request({
				url: 'https://api.bzqll.com/music/netease/search',
				data: {
					key:this.key,
					s,
					limit:20,
					offset
				}
			});












		},
		searchFn(){
			clearTimeout(this.timer)
			this.timer = setTimeout(()=>{
				this.dataLen = 0
				this.getSearchData({
					s:this.searchVal,
					offset:this.dataLen
				}).then(res=>{
					if(res[1].data.code == 200){
						this.songs = res[1].data.data;
						uni.hideLoading ()
					}else{
						this.songs = null
						throw  new URIError('请求error,搜索不到内容');
					}
				})
			},1000)
		}
	},
	onPullDownRefresh(){
		this.dataLen = 0
		this.getSearchData({
			s:this.searchVal,
			offset:this.dataLen
		}).then(res=>{
			if(res[1].data.code == 200){
				this.songs = res[1].data.data;
				uni.hideLoading ()
				uni.stopPullDownRefresh()
			}else{
				this.songs = null
				throw  new URIError('请求error,搜索不到内容');
			}
		})
	},
	onReachBottom(){
		this.dataLen+=20
		this.getSearchData({
			s:this.searchVal,
			offset:this.dataLen
		}).then(res=>{
			if(res[1].data.code == 200){
				this.songs.push(...res[1].data.data);
				uni.hideLoading ()
			}else{
				uni.showToast({
					title: '已无更多内容',
					icon:'none',
					duration: 2000
				});
			}
		})
	},
	components:{
		'song-list':SongList
	}
}
